import React, { Component } from 'react'
import { Tabs } from 'antd'
const TabPane = Tabs.TabPane

import BaseForm from '/page/case/caseForm.jsx'
import PriceForm from '/page/case/caseMachine.jsx'

class CaseIndex extends Component{


	state = {
		active: 1,
		id: undefined
	}

	saveDone = id => this.setState({id})

	render = _ => <div style={{padding: '0 15px', height: '100%'}}>
		<Tabs 
		className={'goodTabs'}
		defaultActiveKey="2">
			<TabPane tab="基本信息" key="1">
				<div style={{paddingBottom: 76}}>
					<BaseForm id={this.state.id} saveDone={this.saveDone}/>
				</div>
			</TabPane>
			<TabPane tab="设备清单" key="2">
				<div style={{paddingBottom: 76}}>
					<PriceForm id={this.state.id} saveDone={this.saveDone}/>
				</div>
			</TabPane>
		</Tabs>
	</div>
}

export default CaseIndex


