import {
  Form, Row, Col, Input, Button, Icon,
} from 'antd';

const data = [{label: '合同号', value: 'HT112120924'},
{label: '项目编号', value: '101011210004'},
{label: '项目名称', value: '关键服务器设备第三方维护'},
{label: '合同名称', value: '关键服务器设备第三方维护合同'},
{label: '合同金额', value: '822888.00'},
{label: '起始日期', value: '2019-01-02'},
{label: '结束日期', value: '2019-05-02'},
{label: '签订日期', value: '2019-01-01'},
{label: '业务类别', value: '支持与维护服务'},
{label: '行业类别', value: '银行'},
{label: '业务员', value: '刘亚男'},
{label: '服务类别', value: '支持与维护服务'},
{label: '合同状态', value: '审核通过'},
{label: '立项日期', value: '2019-02-01'},
{label: '客户', value: '国家开发银行股份有限公司'},
{label: '部门', value: '华东一部'},
{label: '合同期限(月)', value: '4'},
{label: '纸质合同份数', value: '2'},
{label: '备注', value: ''},
{label: '合同系数', value: '1'}]
class CaseForm extends React.Component {
  state = {
    expand: false,
  };

  // To generate mock Form.Item
  getFields() {
    const count = this.state.expand ? 10 : 6;
    const children = [];
    for (let i = 0; i < data.length; i++) {
      children.push(
        <Col span={8} key={i} style={{ display: i < count ? 'block' : 'block' }}>
          <Form.Item label={data[i].label}>
            <span>{data[i].value}</span>
          </Form.Item>
        </Col>
      );
    }
    return children;
  }

  handleSearch = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  }

  handleReset = () => {
    this.props.form.resetFields();
  }

  toggle = () => {
    const { expand } = this.state;
    this.setState({ expand: !expand });
  }

  render() {
    return (
      <Form
        className="ant-advanced-search-form"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24}>{this.getFields()}</Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            
          </Col>
        </Row>
      </Form>
    );
  }
}


export default CaseForm


