import React from 'react'
import { Button, Modal, Input, Icon } from 'antd'
import Common from '/page/common.jsx'

class CaseMachine extends Common{

	async componentWillMount () {
		this.search()
	}

	state = Object.assign({}, this.state, {
		search: Object.assign({}, this.state.pageconf),
		columns: [{
			title: '设备编码',
			dataIndex: 't1'
		},{
			title: '设备名称',
			dataIndex: 't2'
		},{
			title: '计量单位',
			dataIndex: 't3'
		},{
			title: '数量',
			dataIndex: 't4'
		},{
			title: '单价',
			dataIndex: 't5'
		},{
			title: '金额',
			dataIndex: 't6'
		},{
			title: '税率',
			dataIndex: 't7'
		},{
			title: '无税金额',
			dataIndex: 't8'
		},{
			title: '税额',
			dataIndex: 't9'
		},{
			title: '无税单价',
			dataIndex: 't10'
		},{
			title: '是否赠送',
			dataIndex: 't11'
		}],
		modalConf: {visible: false, item: {}}
	})

	search = async _ => {
		let data = [{key: 1, t1: '03040003', t2: 'CX4-480', t3: '台', t4: '1', t5: '426100.00', t6: '426100.00', t7: '17.00', t8: '364188.03', t9: '61911.97', t10: '364188.03', t11: '否'},
		{key: 1, t1: '03040003', t2: 'CX4-480', t3: '台', t4: '1', t5: '426100.00', t6: '426100.00', t7: '17.00', t8: '364188.03', t9: '61911.97', t10: '364188.03', t11: '否'},
		{key: 1, t1: '03040003', t2: 'CX4-480', t3: '台', t4: '1', t5: '426100.00', t6: '426100.00', t7: '17.00', t8: '364188.03', t9: '61911.97', t10: '364188.03', t11: '否'}]
		this.setState({
				tabledata: data, 
				loading: false,
				pagination: Object.assign({}, this.state.pagination, {
					total: 7,
					current: 0
				})
			})
	}

}

export default CaseMachine


